import React from 'react';

import reactIcon from '../static/react-logo.svg';
import gatsbyIcon from '../static/gatsby.svg';
import graphqlIcon from '../static/graphql.svg';
import awsIcon from '../static/aws.svg';
import cssIcon from '../static/css.svg';
import cypressIcon from '../static/cypress-io.svg';
import expressIcon from '../static/express.svg';
import javascriptIcon from '../static/javascript.svg';
